import React, { useState, useEffect } from 'react'
import Number from './components/Number'
import Notification from './components/Notification'
import numberService from './services/numbers'

const App = () => {
  const [numbers, setNumbers] = useState([]) 
  const [newNumber, setNewNumber] = useState('')
  const [newName, setNewName] = useState('')
  const [errorMessage, setErrorMessage] = useState(null)

  useEffect(() => {
    numberService
      .getAll()
      .then(initialNumbers => {
        setNumbers(initialNumbers)
      })
  }, [])

  const addEntry = (event) => {
    event.preventDefault()
    const numberObject = {
      name: newName,
      number: newNumber
    }
    let doesExist =numbers.find(entry=>entry.name===newName)
    console.log(doesExist, ' ee')
    if (!doesExist){

        numberService
        .create(numberObject)
        .then(returnedNumber => {
            
            setNumbers(numbers.concat(returnedNumber))
            setNewName('')
            setNewNumber('')
        })
        .catch(e=>setErrorMessage(e))
    }else{
        numberService
        .update(doesExist.id,numberObject)
        .then(updatedNumber=>{
            const updatedNumbers = numbers.map(entry=>{
                if (entry.name!==newName){
                    return entry
                }else{
                    return updatedNumber
                }
            })
            setNewName('')
            setNewNumber('')
            setNumbers(updatedNumbers)
        })
        .catch(e=>setErrorMessage(e))
    }
  }

//   const toggleImportanceOf = (id) => {
//     const url = `http://localhost:3001/notes/${id}`
//     const note = notes.find(n => n.id === id)
//     const changedNote = { ...note, important: !note.important }
  
//     numberService
//       .update(id, changedEntry)
//       .then(returnedEntry => {
//         setNumbers(numbers.map(entry => entry.id !== id ? entry : returnedEntry))
//       })
//       .catch(error => {
//         setErrorMessage(
//           `entry '${note.content}' was already removed from server`
//         )
//         setTimeout(() => {
//           setErrorMessage(null)
//         }, 5000)   
//       })
//   }

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value)
  }
  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  return (
    <div>
      <h1>Numbers</h1>
      <Notification message={errorMessage} />     
      <ul>
        {numbers.map((entry, i) => 
          <Number
            key={i}
            number={entry.number} 
            name={entry.name}
          />
        )}
      </ul>
      <form onSubmit={addEntry}>
        <input
          type='phone'
          placeholder='Enter phone number'
          value={newNumber}
          onChange={handleNumberChange}
        />
        <br/>
        <input
          type='text'
          placeholder='Enter Name'
          value={newName}
          onChange={handleNameChange}
        />
        <button type="submit">save</button>
      </form>   
    </div>
  )
}

export default App 